.todoFormContainer {
  margin: auto;
  margin-bottom: 30px;
  margin-top: 50px;
  width: 50%;
}
.todoFormContainer form {
  display: flex;
  align-items: center;
  justify-content: center;
}
.todoFormContainer input {
  width: 45%;
  min-width: 30%;
  height: 30px;
  font-size: 1.3rem;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
}
